.full_container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
body {
  width: 100%;
}

.menu_container {
  display: flex;
  justify-content: space-between;
  padding-top: 30px;
  padding-bottom: 30px;
  position: fixed;
  width: 100vw;
  z-index: 10;
  background: white;
  top: 0;
  box-shadow: 1px 1px black;
}
.menu_home {
  margin-left: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.menu_now {
  display: flex;
  align-items: center;
  font-family: "Titillium Web", "san-selif";
  font-weight: 900;
  color: rgba(96, 74, 138);
  font-size: 16px;
  -webkit-transition: 1s;
  transition: 1s;
}
.menu_menu {
  margin-right: 40px;
  display: flex;
  align-items: center;
  cursor: pointer;
}
@media (max-width: 770px) {
  .menu_container {
    padding-top: 20px;
    padding-bottom: 20px;
  }
}
@media (max-width: 380px) {
  .menu_home {
    margin-left: 30px;
  }
  .menu_menu {
    margin-right: 30px;
  }
}

.home_container {
}
.home_first_container {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  width: 100vw;
  font-family: "Titillium Web", "san-selif";
  font-weight: 900;
  color: white;
  background: url(/portfoliov2/static/media/background.79437988.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  font-size: 40px;
}
.home_first_container .home_first_title{
  margin-left: -20vw;
}
.home_first_container_br {
  display: inline;
}
.home_about_container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  margin-bottom: 120px;
  width: 100vw;
  padding-top: 20px;
  padding-bottom: 20px;
  /* background: rgb(227, 173, 248); */
}
.home_about_title {
  background: rgb(152, 129, 194);
  width: 100vw;
  display: flex;
  justify-content: center;
  font-family: "Titillium Web", "san-selif";
  color: white;
  font-weight: 900;
  font-size: 4px;
  letter-spacing: 2px;
}
.home_about_desc {
  position: relative;
  width: 400px;
  height: 400px;
}
.home_about_image {
  height: 400px;
}
.home_about_desc_head {
  color: rgba(96, 74, 138);
  font-family: "escore9";
  font-size: 40px;
  margin-top: 20px;
  margin-bottom: 10px;
}
.home_about_desc_body {
  font-family: "escore4";
  font-size: 15px;
  line-height: 30px;
  padding-right: 90px;
}
.home_about_desc_foot {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 30px;
  position: absolute;
  bottom: 100px;
  left: 0;
}
.home_about_desc_foot_sns {
  display: flex;
}
.home_about_desc_foot_skills  {
  display: flex;
}
.home_about_desc_foot_skills {
  margin-top: 10px;
}
.home_project_title {
  width: 100vw;
  display: flex;
  justify-content: center;
  font-family: "Titillium Web", "san-selif";
  color: rgba(96, 74, 138);
  font-weight: 900;
  font-size: 70px;
}
.home_project_subtitle {
  width: 100vw;
  display: flex;
  justify-content: center;
  font-family: "escore7";
  align-self: center;
  font-size: 13px;
  color: rgb(152, 129, 194);
  margin-bottom: 40px;
}
.home_project_container {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
}
.home_project_item {
  position: relative;
  margin-bottom: 30px;
}
.home_project_item_desc {
  position: absolute;
  color: white;
  left: 30px;
  bottom: 30px;
}
.home_project_item_desc2 {
  position: absolute;
  color: white;
  right: 30px;
  bottom: 30px;
}
.project_image {
  width: 720px;
  cursor: pointer;
}
.project_image:hover {
  /* opacity: 10%; */
}

@media (max-width: 1070px) {
  .home_container {
    /* margin-top: 120px; */
  }
  .home_first_container .home_first_title{
    margin-left: 0;
  }
  .home_about_desc {
    width: 600px;
  }
  .home_about_container {
    flex-direction: column;
    align-items: center;
  }
  .home_about_desc_foot {
    margin-bottom: 40px;
  }
  
}
@media (max-width: 770px) {
  .home_first_container .home_first_title{
    margin-left: -5vw;
  }
  .home_about_desc {
    width: 80vw;
    justify-self: center;
  }
  .home_about_desc_body {
    padding-right: 0px;
  }
  .home_about_image {
    width: 100vw;
    height: auto;
  }
  .home_first_container_br {
    display: block;
  }
  .home_about_title{
    font-size: 50px;
  }
  .home_about_desc {
    display: flex;
    flex-direction: column;
  }
  .home_about_desc_foot {
    margin-top: 20px;
    position: static;
    margin-bottom: 20px;
  }
  .home_project_title{
    font-size: 50px;
  }
  .project_image {
    width: 100vw;
    height: auto;
  }
  .home_project_item {
    display: flex;
    flex-direction: column;
  }
  .home_project_item_desc {
    position: absolute;
    color: white;
    left: 30px;
    bottom: 30px;
    margin-bottom: 30px;
    font-size: 15px;
  }
  .home_project_item_desc2 {
    position: absolute;
    color: white;
    left: 30px;
    bottom: 30px;
    font-size: 15px;
  }
}
@media (max-width: 380px) {
  .home_first_container{
    font-size: 40px;
  }
  .home_about_desc_foot_skills {
    margin-top: 40px;
  }
}


